<template>
  <div>
    <validation-observer ref="observer" v-slot="{ invalid }">
      <el-row :gutter="10">
        <el-col :span="12" :xs="24">
          <el-form label-width="80px" v-loading="loading">
            <el-card v-if="!formConfig.length">
              <div class="placeholder"></div>
            </el-card>
            <form-builder
              @command="handleCommand"
              :form="form"
              :shares="formShares"
              :config="formConfig"
              :metadata="metadata"
            ></form-builder>
            <app-form-action>
              <el-button :disabled="loading" type="primary" @click="save">
                
              </el-button>
            </app-form-action>
          </el-form>
        </el-col>
        <el-col :span="12" :xs="24">
          <el-card header="" class="form-builder-card">
            <el-input type="textarea" v-model="formConfigJSON" rows="45" />
          </el-card>
        </el-col>
      </el-row>
    </validation-observer>
    <el-dialog title="" :visible.sync="showResultModal" width="80%">
      <pre>{{ JSON.stringify(formValues, null, 4) }}</pre>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="showResultModal = false"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<style lang="scss" scoped>
.el-card.form-builder-card {
  ::v-deep .el-card__header {
    padding: 10px 20px;
  }
}
</style>

<script>
import { useForm, useLoading } from '@fext/vue-use';
import { createFormBuilder } from 'element-form-builder';
import { ElFormAdaptor } from 'element-form-builder/lib/adaptor/element';
import ExampleFormComponents from './components';

export default {
  name: 'example-basic-form',

  components: {
    FormBuilder: createFormBuilder({
      components: {
        ElFormAdaptor,

        ...ExampleFormComponents
      }
    })
  },

  setup() {
    const form = useForm();
    const { formValues, setInitialFormValues, updateFormValues } = form;
    const { loading, withLoading } = useLoading();

    return {
      // from form composition
      form,
      formValues,
      setInitialFormValues,
      updateFormValues,

      // from loading composition
      loading,
      withLoading
    };
  },

  data() {
    return {
      showResultModal: false,

      metadata: {},

      formShares: {
        size: 'medium',

        props: {
          clearable: true
        }
      },

      formConfig: [],

      formConfigJSON: JSON.stringify(
        [
          {
            component: 'el-card',
            props: {
              header: ''
            },
            fields: [
              {
                name: 'channel',
                component: 'ExampleChannel',
                rules: {
                  required: true
                }
              },
              {
                name: 'name',
                component: 'ExampleName',
                rules: {
                  required: true
                }
              },
              {
                name: 'comment',
                component: 'ElFormAdaptor',
                label: '',
                tip: ' FormAdaptor ',
                tooltip: '',
                rules: {
                  required: true,
                  max: 50,
                  min: 10
                },
                props: {
                  placeholder: ' 20 '
                }
              }
            ]
          },
          {
            component: 'el-card',
            props: {
              header: ''
            },
            fields: [
              {
                name: 'type',
                component: 'ExampleType',
                defaultValue: 2
              },
              {
                name: 'actor',
                component: 'ExampleActorComplex'
              },
              {
                name: 'date',
                component: 'ElFormAdaptor',
                label: '',
                extend: {
                  component: 'el-date-picker'
                },
                props: {
                  placeholder: ''
                }
              },
              {
                name: 'description',
                component: 'ElFormAdaptor',
                label: '',
                tip: ' FormAdaptor ',
                rules: {
                  max: 180
                },
                props: {
                  type: 'textarea',
                  rows: 5,
                  placeholder: ' 180 '
                }
              }
            ]
          }
        ],
        null,
        2
      )
    };
  },

  watch: {
    formConfigJSON: {
      handler(value) {
        try {
          this.formConfig = JSON.parse(this.formConfigJSON);
        } catch (err) {
          // this.formConfig = this.formConfig;
        }
      },
      immediate: true
    }
  },

  created() {
    this.registerEvents();
    this.renderForm();
  },

  methods: {
    delay(timeout = 1000) {
      return new Promise(resolve => {
        setTimeout(() => {
          resolve();
        }, timeout);
      });
    },

    getInitialValues() {
      return {};
    },

    registerEvents() {
      this.$on('doSomething', options => {
        this.$message({
          type: 'success',
          message: ''
        });
      });
    },

    handleCommand(cmd, ...args) {
      this.$emit(cmd, ...args);
    },

    async renderForm() {
      return this.withLoading(() => {
        return Promise.all([this.getRenderMetadata(), this.getFormValues()]);
      });
    },

    async getRenderMetadata() {
      await this.delay();

      // fake render metadata
      return Promise.resolve({
        channels: [
          {
            id: 1,
            code: 'dy',
            name: ''
          },
          {
            id: 2,
            code: 'dm',
            name: ''
          }
        ]
      }).then(data => {
        this.metadata = data;
      });
    },

    async getFormValues() {
      await this.delay();

      // fake form values
      return Promise.resolve({
        channel: 2,
        name: 'Form Builder',
        comment: 'A powerful form builder',
        actor: ['Yang', 'Zhang'],
        description: 'What a nice tool'
      }).then(data => {
        const { formValues } = this;

        this.updateFormValues({
          ...formValues,
          ...data
        });
      });
    },

    async save() {
      const valid = await this.$refs.observer.validate();

      if (!valid) {
        this.$message({
          type: 'error',
          message: ''
        });
        return;
      }

      this.showResultModal = true;
    }
  }
};
</script>
