<template>
  <validation-provider :rules="rules" v-slot="{ errors }">
    <el-form-item label="" size="medium" :required="isRequired">
      <el-checkbox-group :value="localValue" @input="updateLocalValue">
        <el-checkbox :label="1"></el-checkbox>
        <el-checkbox :label="2"></el-checkbox>
        <el-checkbox :label="4"></el-checkbox>
        <el-checkbox :label="5"></el-checkbox>
        <el-checkbox :label="6"></el-checkbox>
        <el-checkbox :label="7"></el-checkbox>
        <el-checkbox :label="8"></el-checkbox>
        <el-checkbox :label="9"></el-checkbox>
        <el-checkbox :label="10"></el-checkbox>
        <el-checkbox :label="11"></el-checkbox>
        <el-checkbox :label="12"></el-checkbox>
      </el-checkbox-group>
      <app-form-error :error="errors[0]"></app-form-error>
    </el-form-item>
  </validation-provider>
</template>

<style lang="scss" scoped></style>

<script>
import { useFormElement } from '@fext/vue-use';

export default {
  name: 'example-category',

  props: {
    name: String,
    rules: {
      type: [String, Object]
    },
    value: {
      required: false
    },
    formValues: {
      type: Object,
      required: false
    }
  },

  setup(props, context) {
    const {
      dirty,
      isRequired,
      localValue,
      setInitialValue,
      updateLocalValue
    } = useFormElement(props, context);

    return {
      dirty,
      isRequired,
      localValue,
      setInitialValue,
      updateLocalValue
    };
  },

  data() {
    return {};
  },

  created() {
    this.setInitialValue([]);
  }
};
</script>
