<template>
  <validation-provider :rules="rules" v-slot="{ errors }">
    <el-form-item
      label=""
      size="medium"
      :required="isRequired"
      :error="errors[0]"
    >
      <el-input
        clearable
        :value="localValue"
        @input="updateLocalValue"
        type="text"
        placeholder=""
      >
        <template v-slot:append>
          <el-tooltip content="" placement="top">
            <el-button icon="el-icon-phone-outline" @click="doSomething"
              ></el-button
            >
          </el-tooltip>
        </template>
      </el-input>
      <div class="el-form-item__tip" v-if="tip">{{ tip }}</div>
    </el-form-item>
  </validation-provider>
</template>

<style lang="scss" scoped></style>

<script>
import { useFormElement } from '@fext/vue-use';

export default {
  name: 'example-name',

  props: {
    name: String,
    tip: String,
    tooltip: String,
    hide: Boolean,
    rules: {
      type: [String, Object]
    },
    extend: Object,
    metadata: Object,
    value: {
      required: false
    },
    formValues: {
      type: Object,
      required: false
    }
  },

  setup(props, context) {
    const {
      dirty,
      isRequired,
      localValue,
      setInitialValue,
      updateLocalValue
    } = useFormElement(props, context);

    return {
      dirty,
      isRequired,
      localValue,
      setInitialValue,
      updateLocalValue
    };
  },

  data() {
    return {};
  },

  methods: {
    doSomething(evt) {
      this.$emit('command', 'doSomething', { id: 123456 });
    }
  }
};
</script>
